@mixin md {
  @media (min-width: 720px) {
    @content; } }

body {
  min-height: 100vh; }

.container {
  padding-top: 20px;
  margin-top: 20px;
  @include md {
    padding: 0 50px;
    margin-top: 64px; } }

.ant-layout-sider-light {
  display: none;
  @include md {
    display: block; } }
.ant-table {
  .ant-table-expanded-row {
    td {
      white-space: initial; } }
  td {
    white-space: nowrap; } }

